// import  from ""
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			page: 1,
			id: "",
			totalPage: 1,
			currentActive: 0,
			sortType: 0,
			status: 'loading',
			dataList: [],
			navList: [{
					title: "销量",
					flag: true
				},
				{
					title: "价格",
					flag: true
				}
			],
		}
	},
	components: {
		uniLoadMore
	},
	onLoad(options) {
		console.log(options)
		this.id = options.id;
		this.loadData();
	},
	onShow() {},
	onHide() {},
	methods: {
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid,
				categoryId: this.id,
				sortType: this.sortType,
				nowPage: this.page
			}
			console.log(parmas)
			this._reqw.ipost(parmas, "yjCategoryProductList").then(res => {
				res.result != 0 ? (this.$api.tip(res.resultNote), this.status = "noMore") : (this.totalPage = res.totalPage, res
					.dataList != undefined && res.dataList.length != 0 ? (res.dataList
						.forEach(item => {
							this.dataList.push(item)
						}), this.status = "more") : this.status = "noMore")
			}).catch(err => {})
		},
		submitSearch() {
			this.$api.navTo('/pagesA/search/search_index');
		},
		gotoDetail(e) {
			this.$api.navTo(`/pagesA/shopdetails/shopDetails?id=${e.id}`)
		},
		initData() {
			this.page = 1;
			this.totalPage = 1;
			this.status = "loading";
			this.dataList = [];
			this.loadData();
		},
		changeTab(v, k) {
			console.log(k)
			this.currentActive != k ? this.currentActive = k : v.flag = !v.flag;
			switch (k) {
				case 0:
					v.flag ? this.sortType = 0 : this.sortType = 1;
					this.initData();
					break;
				case 1:
					v.flag ? this.sortType = 2 : this.sortType = 3;
					this.initData();
					break;
			}
		},
	},
	//  加载
	onReachBottom() {
		console.log("到底")
		this.status = "loading";
		this.totalPage > this.page ? (this.page += 1, this.loadData()) : setTimeout(() => {
			this.status = "noMore"
		}, 30)
	},

}
